.picture-container
{
    position: relative;
    width:80%;
    
}

.picture-container .polaroid
{
    position: absolute;
    border: 1px solid darkgrey;
    background-color: white;
    padding: 15px 15px 60px 15px;
    box-shadow: 5px 10px 10px darkgrey;
}

.picture-container .polaroid:after{
    content: attr(polaroid-caption);
    position: absolute;
    bottom: 0px;
    left: 0px;
    color:black;
    width: calc(100% - 20px);
    padding: 10px;
    font-size: 30px;
    text-align: center;
    font-family: 'Indie Flower', cursive;
}

.picture-container .polaroid-small:after{
    content: attr(polaroid-caption);
    position: absolute;
    bottom: 0px;
    left: 0px;
    color:black;
    width: calc(100% - 20px);
    padding: 10px;
    font-size: 30px;
    text-align: center;
    font-family: 'Indie Flower', cursive;
}

.picture-container .polaroid-small
{
    width: 500px;
    left: -200px;
    position: absolute;
    border: 1px solid darkgrey;
    background-color: white;
    padding: 15px 15px 60px 15px;
    box-shadow: 5px 10px 10px darkgrey;
}

.picture-container .polaroid-shmedium
{
    width: 350px;
    left: 50px;
    top: 25px;
    position: absolute;
    border: 1px solid darkgrey;
    background-color: white;
    padding: 15px 15px 60px 15px;
    box-shadow: 5px 10px 10px darkgrey;
    transform: rotate(20deg);
    -ms-transform: rotate(20deg);
    -webkit-transform: rotate(20deg);
}


.picture-container .image1
{
    top: -135px;
    width: 70%;
    left: 100px;
    transform: rotate(-10deg);
    -ms-transform: rotate(-10deg);
    -webkit-transform: rotate(-10deg);
}

.picture-container .image2
{
    right: 15%;
    top: -50px;
    transform: rotate(20deg);
    -ms-transform: rotate(20deg);
    -webkit-transform: rotate(20deg);
}


@media (max-width: 767px) {
    .picture-container {
        position: relative;
        width: 130%;
        height: 0px;
        left: -105px;
        top: 55px;
    }

    .picture-container .polaroid
    {
        position: absolute;
        border: 1px solid darkgrey;
        background-color: white;
        padding: 10px 10px 20px 10px;
        box-shadow: 5px 10px 10px darkgrey;
    }

    .picture-container .image2
    {
        top: -120px;
        left: 120px;
        width: 155px;
        transform: rotate(20deg);
        -ms-transform: rotate(20deg);
        -webkit-transform: rotate(20deg);
    }

    .picture-container .image1
    {   
        width:135px;
        transform: rotate(-10deg);
        -ms-transform: rotate(-10deg);
        -webkit-transform: rotate(-10deg);
    }

    .picture-container .polaroid-shmedium
    {
        position: absolute;
        border: 1px solid darkgrey;
        background-color: white;
        padding: 10px 10px 20px 10px;
        box-shadow: 5px 10px 10px darkgrey;
        margin-left: 70px;
        width: 125px;
        top: -45px;
    }


    .picture-container .polaroid-small
    {
        position: absolute;
        border: 1px solid darkgrey;
        background-color: white;
        padding: 15px 15px 50px 15px;
        box-shadow: 5px 10px 10px darkgrey;
        margin-left: 205px;
        width: 200px;
        top: -50px;
    }

    .picture-container .polaroid-small:after{
        content: attr(polaroid-caption);
        position: absolute;
        left: 5px;
        color:black;
        width: calc(100% - 20px);
        padding: 10px;
        font-size: 18px;
        text-align: center;
        font-family: 'Indie Flower', cursive;
    }
    
}